@use "sass:math";

/* PC/Heading_1 */
@mixin pc-heading-1 {
  font-size: 3rem;
  font-weight: 500;
  line-height: math.div(45, 30);
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* Story/PC/Title - Heading_1 */
@mixin story-pc-heading-1 {
  font-size: 4rem;
  font-weight: bold;
  line-height: math.div(64, 40);
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* Story/SP/Title - Heading_1 */
@mixin story-sp-heading-1 {
  font-size: 2.8rem;
  font-weight: bold;
  line-height: math.div(45, 28);
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* SP/Heading_1 */
@mixin sp-heading-1 {
  font-size: 2.8rem;
  font-weight: 500;
  line-height: math.div(45, 28);
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* PC/Heading_2 */
/* SP/Heading_2 */
@mixin heading-2 {
  font-weight: 500;
  font-size: 2.6rem;
  line-height: math.div(42, 26);
  text-align: center;
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* Story/PC/Heading_2 */
@mixin story-pc-heading-2 {
  font-size: 2.6rem;
  font-weight: bold;
  line-height: math.div(42, 26);
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* Story/SP/Heading_2 */
@mixin story-sp-heading-2 {
  font-size: 2.2rem;
  font-weight: bold;
  line-height: math.div(35, 22);
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* PC/Heading_3 */
/* SP/Heading_3 */
@mixin heading-3 {
  font-size: 2.2rem;
  font-weight: 500;
  line-height: math.div(33, 22);
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* PC/Heading_4 */
/* SP/Heading_4 */
@mixin heading-4 {
  font-weight: 500;
  font-size: 2rem;
  line-height: math.div(30, 20);
  letter-spacing: 0.05em;
  font-feature-settings: "palt" on;
}

/* PC/Sub Title */
@mixin pc-sub-title {
  font-size: 1.8rem;
  font-weight: normal;
  line-height: 2;
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* SP/Sub Title */
@mixin sp-sub-title {
  font-size: 1.6rem;
  font-weight: normal;
  line-height: 2;
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* PC/Sub Title_Bold */
@mixin pc-sub-title-bold {
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 2;
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* SP/Sub Title_Bold */
@mixin sp-sub-title-bold {
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 2;
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* PC/Caption */
/* SP/Caption */
@mixin caption {
  font-size: 1.2rem;
  font-weight: normal;
  line-height: 1.8;
  letter-spacing: 0.05em;
  font-feature-settings: "palt" on;
}

/* PC/Body */
/* SP/Body */
@mixin body {
  font-size: 1.6rem;
  font-weight: normal;
  line-height: 2;
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* PC/Body_Bold */
@mixin body-bold {
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 2;
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* PC/Body Small Bold */
@mixin pc-body-small-bold {
  font-size: 1.4rem;
  font-weight: 500;
  line-height: math.div(25, 14);
  letter-spacing: 0.05em;
  font-feature-settings: "palt" on;
}

/* PC/Body_Small */
/* SP/Body Small */
@mixin body-small {
  font-size: 1.4rem;
  font-weight: normal;
  line-height: math.div(25, 14);
  letter-spacing: 0.05em;
  font-feature-settings: "palt" on;
}

