  font-weight: 500;
  line-height: math.div(21, 14);
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* Parts/Button Label */
@mixin button-label {
  font-size: 1.6rem;
  font-weight: 500;
  line-height: math.div(20, 16);
  letter-spacing: 0.1em;
}

/* Parts/Button Label Small */
@mixin button-label-small {
  font-size: 1.4rem;
  font-weight: 500;
  line-height: math.div(18, 14);
  letter-spacing: 0.05em;
  font-feature-settings: "palt" on;
}

/* Parts/Tag */
@mixin tag {
