  font-weight: normal;
  line-height: math.div(18, 14);
  letter-spacing: 0.05em;
  font-feature-settings: "palt" on;
}

/* Lead copy_Medium */
@mixin lead-copy-medium {
  font-family: var(--font-serif-jp);
  font-style: normal;
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 2;
  text-align: justify;
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}

/* Lead copy_Large */
@mixin lead-copy-large {
  font-family: var(--font-serif-jp);
  font-style: normal;
  font-weight: 500;
  font-size: 1.8rem;
  line-height: 2;
  text-align: justify;
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;
}
