@use "sass:math";
@use "../../foundation/libs/index" as *;

.p-gallery__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, 205px);
  grid-auto-rows: 205px;
  gap: 38px;

  @include mq(md) {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: calc((100vw - 19px - (20px * 2)) / 2);
    gap: 19px;
  }
}

.p-gallery__item {
  width: 100%;
  height: 100%;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.p-gallery__caption {
  margin-block-start: 12px;
  color: var(--color-primary-white);
  @include caption;
}
