@use "../../foundation/libs/index" as *;

.p-service {
  display: grid;
  row-gap: 20px;
}

.p-service__item {
  display: grid;
  position: relative;
  grid-template-columns: 1fr 202px;
  grid-template-rows: min-content min-content 1fr;
  grid-template-areas: "heading image" "text image" "tag image";
  column-gap: 40px;
  padding: 12px 12px 12px 20px;
  border: 1px solid var(--color-other-greige2);
  border-radius: 2px;
  background-color: var(--color-primary-white);

  @include mq(md, min) {
    display: flex;
    justify-content: space-between;
  }

  @include mq(md) {
    grid-template-columns: 100%;
    grid-template-rows: repeat(4, min-content);
    grid-template-areas: "image" "heading" "text" "tag";
    padding: 20px;
  }

  &.is-hidden {
    @include mq(md) {
      display: none;
    }
  }
}

.p-service__text-block {
  @include mq(md, min) {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

.p-service__heading {
  grid-area: heading;
  @include heading-4;
  word-break: break-all;

  @include mq(md) {
    margin-block-start: 16px;
  }
}

.p-service__link {
  display: block;

  &::after {
    content: "";
    position: absolute;
    inset: 0;
  }
}

.p-service__text {
  grid-area: text;
  margin-block-start: 8px;
  @include body-small;

  @include mq(md) {
    margin-block-start: 12px;
  }

  br {
    @include mq(md) {
      display: none;
    }
  }
}

.p-service__tag-list {
  display: flex;
  flex-wrap: wrap;
  grid-area: tag;
  margin-block-start: 12px;
  line-height: 1;

  @include mq(md) {
    margin-block-start: 16px;
    row-gap: 8px;
  }
}

.p-service__tag-item {
  color: var(--color-other-darkBrown);
  font-size: 1.1rem;
  font-weight: 500;
  letter-spacing: 0.1em;

  &:nth-of-type(n + 2) {
    &::before {
      content: "/";
    }
  }
}

.p-service__media {
  grid-area: image;
}

.p-service__image {
  width: 150px;
  height: 150px;
  object-fit: cover;

  @include mq(md) {
    width: 100%;
    height: 100%;
  }
}
