@use "../../foundation/libs/index" as *;

.p-service-detail-header {
  padding-block: calc(138px + var(--height-header)) 220px;
  background-size: 500px 426px;
  background-position: top 116px left calc(50% - 2px + 272px);
  background-repeat: no-repeat;
  background-blend-mode: multiply;

  @include mq(md) {
    padding-block: calc(62px + var(--height-header)) 436px;
    background-size: 375px 320px;
    background-position: center bottom 47px;
  }
}

.p-service-detail-header__heading {
  @include mq(md, min) {
    width: 460px;
  }

  @include mq(md) {
    row-gap: 20px;
  }
}
