@use "../../foundation/libs/index" as *;

.mfp-bg {
  background: var(--color-primary-green-70);
  backdrop-filter: blur(4px);
  opacity: 1;
}

.mfp-container {
  position: fixed;
  padding-inline: 20px;

  @include mq(md, min) {
    padding: 68px 173px 56px;
  }
}

.mfp-content {
  max-width: 934px;
  text-align: center;

  @include mq(md) {
    margin-top: -114px;
  }

  img {
    min-height: 150px;

    @include mq(md, min) {
      max-height: calc(100vh - 68px - 172px);
    }

    @include mq(md) {
      max-height: calc(100vh - 54px - 312px);
    }
  }
}

.mfp-close {
  width: auto;
  opacity: 1;
  font-family: inherit;
  @include button-label;

  @include mq(md, min) {
    position: static;
    margin-block-start: 20px;
  }

  @include mq(md) {
    position: fixed;
    top: auto;
    right: auto;
    bottom: 42px;
    left: 50%;
    transform: translateX(-50%);
  }

  .mfp-close-btn-in & {
    display: inline-flex;
    align-items: center;
    gap: 12px;
    color: var(--color-primary-white);
  }
}

.mfp-arrow {
  top: auto;
  bottom: 0;
  width: auto;
  height: 30px;
  margin: 0;
  color: var(--color-primary-white);
  opacity: 1;
  @include button-label-small;
  transform: none;

  @include mq(md, min) {
    top: 50%;
    transform: translateY(-50%);
  }

  @include mq(md) {
    width: 83px;
    bottom: 106px;
  }

  &:active {
    margin: 0;
  }

  &::before,
  &::after {
    border: 0;
    opacity: 1;
  }

  &::before {
    position: absolute;
    top: 50%;
    width: 30px;
    height: 30px;
    margin: 0;
    border-radius: 50%;
    border: 1px solid;
    background-image: url(../assets/static/images/service/icon_chevron.svg);
    background-repeat: no-repeat;
    background-position: center;
  }
}

.mfp-arrow-left {
  left: 30px;

  @include mq(md) {
    left: calc(50% - 12px);
    transform: translateX(-100%);
  }

  &::before {
    right: auto;
    left: 0;
    transform: translateY(-50%) rotate(0.5turn);
  }
}

.mfp-arrow-right {
  right: 30px;

  @include mq(md) {
    right: calc(50% - 12px);
    justify-content: flex-end;
    transform: translateX(100%);
  }

  &::before {
    right: 0;
    left: auto;
    transform: translateY(-50%);
  }
}

button.mfp-arrow-left {
  display: inline-flex;
  align-items: center;
  padding-left: 38px;
}

button.mfp-arrow-right {
  display: inline-flex;
  align-items: center;
  padding-right: 38px;
}
