@use "sass:math";
@use "../../foundation/libs/index" as *;

.p-post-body {
  padding-block: 56px 80px;

  @include mq(md) {
    padding-block-start: 60px;
  }
}

:where(.p-post-body) {
  .p-post-body__main-image {
    margin-block-end: 48px;
    text-align: center;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  h2:not([class]) {
    padding-block-start: 24px;
    border-top: 1px solid var(--color-primary-green-20);
    @include story-pc-heading-2;

    @include mq(md) {
      @include story-sp-heading-2;
    }

    &:not(:first-child) {
      margin-block-start: 100px;

      @include mq(md) {
        margin-block-start: 60px;
      }
    }
  }

  h3:not([class]) {
    font-size: 2rem;
    font-weight: bold;
    line-height: 1.5;
    letter-spacing: 0.1em;
    font-feature-settings: "palt" on;

    &:not(:first-child) {
      margin-block-start: 48px;
    }
  }

  h4:not([class]) {
    &::before {
      content: "■";
    }

    &:not(:first-child) {
      margin-block-start: 48px;
    }
  }

  h5:not([class]),
  h6:not([class]) {
    &:not(:first-child) {
      margin-block-start: 48px;
    }
  }

  li {
    list-style: inherit;
  }

  ul:not([class]),
  ol:not([class]) {
    padding-inline-start: 24px;
  }

  p:not([class]),
  ul:not([class]),
  ol:not([class]) {
    color: var(--color-primary-black);

    &:not(:first-child) {
      margin-block-start: 24px;
    }
  }

  h4:not([class]) + p:not([class]),
  h4:not([class]) + ul:not([class]),
  h4:not([class]) + ol:not([class]) {
    margin-block-start: 0;
  }

  figure:not([class]) {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
    margin-block-end: 48px;
    text-align: center;

    &:not(:first-child) {
      margin-block-start: 48px;

      @include mq(md) {
        margin-block-start: 32px;
      }
    }

    img {
      max-height: 450px;

      @include mq(md) {
        max-height: 195px;
      }
    }
  }

  figcaption:not([class]) {
    @include caption;
    color: var(--color-other-darkBrown);
  }

  a:not([class]) {
    color: var(--color-other-textLink);
    transition: opacity 0.7s var(--easeOutQuint);

    &[target="_blank"] {
      padding-right: 24px;
      background-image: url(../assets/static/images/common/icon_external_blue.svg);
      background-position: right center;
      background-repeat: no-repeat;
    }

    &:hover {
      @include hoverable {
        opacity: 0.5;
      }
    }
  }

  blockquote {
    display: grid;
    row-gap: 12px;
    margin-block-end: 48px;
    padding: 24px 24px 24px 60px;
    background-color: var(--color-background-greige);
    background-image: url(../assets/static/images/stories/icon_blockquote.svg);
    background-size: 24px;
    background-position: 24px 24px;
    background-repeat: no-repeat;

    @include mq(md) {
      margin-block-end: 32px;
      padding: 48px 20px 20px;
      background-position: 20px 20px;
      font-size: 1.4rem;
      line-height: math.div(28, 14);
    }

    &:not(:first-child) {
      margin-block-start: 48px;

      @include mq(md) {
        margin-block-start: 32px;
      }
    }

    cite {
      color: var(--color-other-darkBrown);
      font-size: 1.2rem;
      line-height: math.div(18, 12);
      letter-spacing: 0.05em;
      font-feature-settings: "palt" on;

      a:not([class]) {
        color: var(--color-primary-green);
      }
    }
  }
}

.p-post-body__table {
  margin-block-end: 48px;
  overflow: auto;

  &:not(:first-child) {
    margin-block-start: 48px;
  }

  table {
    width: 100%;
  }

  th {
    font-weight: normal;
    background-color: var(--color-background-greige);
  }

  th,
  td {
    color: var(--color-primary-black);
    padding: 8px 16px;
    border: 1px solid var(--color-other-grayBrown);
    text-align: center;
  }
}

.p-post-body__podcast {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  margin-block-end: 48px;
  padding: 32px 32px 40px;
  border-radius: 4px;
  background-color: var(--color-background-greige);

  @include mq(md) {
    padding: 20px;
  }

  &:not(:first-child) {
    margin-block-start: 48px;

    @include mq(md) {
      margin-block-start: 32px;
    }
  }
}

.p-post-body__podcast-caption {
  width: 100%;
  max-width: 660px;
  padding-block: 3px;
  padding-inline-start: 24px + 8px;
  background-image: url(../assets/static/images/stories/icon_podcast.svg);
  background-position: left center;
  background-repeat: no-repeat;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: math.div(18, 14);
  letter-spacing: 0.05em;
  font-feature-settings: "palt" on;
}

.p-post-body__text-box {
  display: grid;
  padding: 28px 32px;
  gap: 10px;
  margin-block-end: 48px;
  border: 1px solid var(--color-other-grayBrown);
  border-radius: 2px;

  @include mq(md) {
    margin-block-end: 32px;
    padding: 20px;
  }

  &.-fill {
    background-color: var(--color-background-greige);
  }

  &:not(:first-child) {
    margin-block-start: 48px;

    @include mq(md) {
      margin-block-start: 32px;
    }
  }
}

.p-post-body__cta-button {
  display: flex;
  flex-direction: column;

  @include mq(md, min) {
    padding-inline: 2px;
  }

  &:not(:first-child) {
    margin-block-start: 64px;
  }
}

.p-post-body__normal-button {
  display: flex;
  justify-content: center;

  &:not(:first-child) {
    margin-block-start: 48px;
  }
}

