@use "../../foundation/libs/index" as *;

.p-story-header {
  padding-block: 80px 40px;

  @include mq(md) {
    padding-block: 60px 24px;
  }
}

.p-story-header__desktop {
  display: grid;
  gap: 16px;
}

.p-story-header__heading {
  @include button-label-small;
}

.p-story-header__tag-list {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.p-story-header__filter-area {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
}

.p-story-header__filter-button {
  display: inline-flex;
  gap: 8px;
  padding: 11px 19px;
  border-radius: 21px;
  border: 1px solid var(--color-background-brownLight);
  @include button-label-small;
}
