@use "sass:math";
@use "../../foundation/libs/index" as *;

.c-radio-tab {
  display: inline-flex;
  cursor: pointer;

  :where(&) svg {
    width: 16px;
    height: 16px;
    margin-bottom: -1px;
  }
}

.c-radio-tab__input {
  @include visually-hidden;
}

.c-radio-tab__label {
  display: inline-flex;
  justify-content: center;
  min-width: 120px;
  margin-block-end: -1px;
  padding: 8px 12px;
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
  border-top: 1px solid transparent;
  border-right: 1px solid transparent;
  border-left: 1px solid transparent;
  background-color: var(--color-other-greige2);
  color: var(--color-other-darkBrown);
  font-family: var(--font-serif);
  font-size: 1.4rem;
  line-height: math.div(18, 14);
  letter-spacing: 0.05em;
  z-index: -1;

  .c-radio-tab__input:checked + & {
    border-color: var(--color-primary-green-20);
    background-color: var(--color-background-beige);
    color: inherit;
    z-index: auto;
  }
}

.c-radio-tab__remove {
  .c-radio-tab__input ~ .c-radio-tab__label & {
    display: none;
  }

  .c-radio-tab__input:checked ~ .c-radio-tab__label & {
    @include visually-hidden;
  }
}
