@use "sass:math";
@use "../../foundation/libs/index" as *;

.p-footer {
  background-color: var(--color-background-brownLight);
  color: var(--color-primary-green);
}

.p-footer__home-link {
  display: inline-block;

  :where(&) > svg {
    width: 180px;
    height: 26px;

    @include mq(md, min) {
      width: 138px;
      height: 20px;
    }
  }
}

.p-footer__related-list {
  font-size: 1.4rem;
  line-height: math.div(25.2, 14);
  padding: 20px 0;
  margin: 30px 0 0;
  border-top: 1px solid var(--color-primary-white);
  border-bottom: 1px solid var(--color-primary-white);

  @include mq(md, min) {
    margin: 0;
    padding: 0 0 0 18%;
    border: 0;
  }

  dt {
    font-family: var(--font-serif);
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 1.25;
    letter-spacing: 0.05em;
    font-feature-settings: "palt" on;
  }

  dd {
    display: flex;
    gap: 5px;
    margin: 12px 0 0;
    letter-spacing: 0.05em;

    &::before {
      content: "・";
    }
  }
}

.p-footer__related-link {
  color: var(--color-primary-green);
  line-height: 1.8;
  transition: opacity 0.7s var(--easeOutQuint);

  &:hover {
    @include hoverable {
      opacity: 0.5;
    }
  }

  :where(&) > svg {
    vertical-align: sub;
  }
}

.p-footer__sns-list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 18px;
  row-gap: 12px;
  padding: 20px 0;
  border-bottom: 1px solid var(--color-primary-white);

  @include mq(md, min) {
    padding: 0;
    border: 0;
  }

  dt {
    font-family: var(--font-serif);
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 1.25;
    letter-spacing: 0.05em;
    font-feature-settings: "palt" on;
    width: 100%;
  }
}

.p-footer__sns-link {
  display: block;
  border-radius: 8px;
  background-color: var(--color-primary-white);
  transition-property: background-color, color;
  transition-duration: 400ms;
  transition-timing-function: var(--easeOutExpo);

  @include mq(md) {
    border-radius: 9px;
  }

  @include hoverable {
    &:hover {
      background-color: var(--color-primary-green);
      color: var(--color-primary-white);
    }
  }

  :where(&) > svg {
    @include mq(md, min) {
      width: 36px;
      height: 36px;
    }
  }
}

.p-footer__copyright {
  margin-block-start: 30px;
  font-family: var(--font-serif);
  font-size: 1rem;
  line-height: math.div(12, 10);
  letter-spacing: normal;

  @include mq(md) {
    font-size: 1.2rem;
    line-height: math.div(15, 12);
  }
}
