@use "sass:math";
@use "../../foundation/libs/index" as *;

/* ------------------------------------------------- *
/*        p-forbiz-kv1
/* ------------------------------------------------- */

.p-forbiz-kv1 {
  color: var(--color-concept-biz-blue);
  background-color: var(--color-background-greige);
  padding: calc(176px + var(--height-header)) 20px 35px;
  background-repeat: no-repeat;
  background-blend-mode: multiply;

  @include mq(md) {
    background-image: url(../assets/static/images/forbiz/hero_sp.png);
    background-size: 240px 240px;
    background-position: right top 60px;
  }

  @include mq(md, min) {
    background-image: url(../assets/static/images/forbiz/hero.png);
    background-position: top 144px left calc(50% - 2px + 312px);
    background-size: 472px 472px;
    padding: calc(108px + var(--height-header)) 0 146px;
  }
}

.p-forbiz-kv1__inner1 {
  @include mq(md, min) {
    max-width: 934px;
    margin: 0 auto;
  }
}

.p-forbiz-kv1__title1 {
  font-weight: 400;
  font-size: 5rem;
  line-height: 63px;
  line-height: math.div(62.7, 50);
  letter-spacing: 0.1em;
  font-family: var(--font-serif);
  br {
    @include mq(md, min) {
      display: none;
    }
  }
}

.p-forbiz-kv1__text1 {
  margin: 28px 0 0;
  @include mq(md, min) {
    margin: 26px 0 0;
    max-width: 550px;
  }
}

/* ------------------------------------------------- *
/*        p-forbiz-block1
/* ------------------------------------------------- */

.p-forbiz-block1 {
  margin: 100px 0 0;
  color: var(--color-concept-biz-blue);
  @include mq(md, min) {
    padding: 36px 0 0;
    border-top: 1px solid var(--color-concept-townDesign-20);
    max-width: 934px;
    margin: 170px auto 0;
    scroll-margin: 400px;
  }
}

.p-forbiz-block1__inner1 {
  padding: 0 20px;
  @include mq(md, min) {
    padding: 0;
  }
}

.p-forbiz-block1__title1 {
  font-weight: 500;
  font-size: 2.6rem;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.1em;
  br {
    @include mq(md, min) {
      display: none;
    }
  }
}

.p-forbiz-block1__text1 {
  @include sp-sub-title-bold;
  margin: 28px 0 0;
  @include mq(md, min) {
    text-align: center;
    @include pc-sub-title-bold;
  }
  br {
    display: none;
    @include mq(md, min) {
      display: block;
    }
  }
}

.p-forbiz-block1__list {
  margin: 60px 0 0;

  @include mq(md, min) {
    margin: 80px 0 0;
  }
}

.p-forbiz-block1__list1 {
  margin: 60px 0 0;
  display: grid;
  gap: 40px;
  @include mq(md, min) {
    margin: 80px 0 0;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 50px;
    row-gap: 40px;
  }
}

.p-forbiz-block1__list1-item {
  @include mq(md, min) {
    height: 264px;
    position: relative;
  }
}

.p-forbiz-block1__unit1__main1 {
  display: block;
}

.p-forbiz-block1__unit1__main1__image1 {
  position: relative;
  aspect-ratio: 278 / 170;
  display: block;
  transition: all 0.7s var(--easeOutQuint);
  overflow: hidden;

  img {
    transition: all 0.7s var(--easeOutQuint);
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  svg {
    position: absolute;
    bottom: 8px;
    right: 8px;
  }
  @at-root {
    .p-forbiz-block1__unit1__main1:hover & img {
      transform: scale(1.1);
    }
  }
}

.p-forbiz-block1__unit1__main1__text1 {
  @include card-title-small;
  display: block;
  margin: 12px 0 0;
}

.p-forbiz-block1__unit1__label1 {
  margin: 8px 0 0;

  @include mq(md, min) {
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

.p-forbiz-block1__button1 {
  margin: 60px 0 0;
  text-align: center;
  @include mq(md, min) {
    margin: 80px 0 0;
  }
}

.p-forbiz-block1__banner1 {
  margin: 60px 0 0;
  text-align: center;

  @include mq(md, min) {
    margin: 80px 0 0;
  }

  a {
    display: block;
    margin: 0 auto;
    transition: all 0.7s var(--easeOutQuint);

    @include mq(md, min) {
      max-width: 748px;
    }

    &:hover {
      opacity: 0.5;
    }
  }
}

.p-forbiz-block1__banner1__sp1 {
  display: block;
  @include mq(md, min) {
    display: none;
  }
}

.p-forbiz-block1__banner1__pc1 {
  display: none;
  @include mq(md, min) {
    display: block;
  }
}

/* ------------------------------------------------- *
/*        p-forbiz-block2
/* ------------------------------------------------- */

.p-forbiz-block2__scroll-margin {
  scroll-margin-top: 130px;

  @include mq(md) {
    scroll-margin-top: 50px;
  }
}

//Edge
@supports (-ms-ime-align: auto) {
  .p-forbiz-block2__scroll-margin {
    scroll-margin-top: 0;

    @include mq(md) {
      scroll-margin-top: 0;
    }
  }
}

_:lang(x)::-ms-,
.p-forbiz-block2__scroll-margin {
  scroll-margin-top: 0;

  @include mq(md) {
    scroll-margin-top: 0;
  }
}

//Safari
_::-webkit-full-page-media,
_:future,
:root .p-forbiz-block2__scroll-margin {
  scroll-margin-top: 0;

  @include mq(md) {
    scroll-margin-top: 40px;
  }
}

//Firefox
@-moz-document url-prefix() {
  .p-forbiz-block2__scroll-margin {
    scroll-margin-top: 0;

    @include mq(md) {
      scroll-margin-top: 40px;
    }
  }
}

