@use "../../foundation/libs/index" as *;

.c-section-header {
  display: grid;
  row-gap: 28px;
  padding-block-start: 28px;
  border-top: 1px solid var(--color-primary-green-20);

  @include mq(md) {
    padding-block-start: 36px;
  }

  &.-md-no-border {
    @include mq(md) {
      border-top: 0;
      padding-block-start: 0;
    }
  }
}

.c-section-header__heading {
  @include heading-2;
}

.c-section-header__lead {
  @include mq(md, min) {
    @include pc-sub-title-bold;
    text-align: center;
  }

  @include mq(md) {
    @include sp-sub-title-bold;
  }
}
