@use "../../foundation/libs/index" as *;

.p-story-body {
  display: grid;
  row-gap: 80px;
  padding-block-start: 40px;
  border-top: 1px solid var(--color-primary-green-20);

  @include mq(md, min) {
    margin-block-end: -70px;
  }

  @include mq(md) {
    row-gap: 60px;
  }
}

.p-story-body__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  column-gap: 50px;
  row-gap: 40px;

  @include mq(md) {
    row-gap: 30px;
  }
}

.p-story-body__footer {
  display: flex;
  justify-content: center;
}

.p-story-body__button {
  &.is-hidden {
    display: none;
  }
}
