@use "../../foundation/libs/index" as *;

.p-story {
  display: flex;
  flex-direction: column;
  position: relative;

  &.is-hidden {
    display: none;
  }
}

.p-story__text {
  margin-block-start: 12px;
  @include card-title-small;
  text-overflow: ellipsis;
  overflow: hidden;
  transition: opacity 0.7s var(--easeOutQuint);

  &:hover {
    @include hoverable {
      opacity: 0.5;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 33px;
    left: 0;
  }
}

.p-story__tag-list {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  line-height: 1;
  margin-block-start: auto;
  padding-block-start: 8px;
}

.p-story__media {
  position: relative;
  aspect-ratio: 278/170;
  order: -1;
  overflow: hidden;
  pointer-events: none;

  @include mq(md) {
    aspect-ratio: 335/205;
  }
}

.p-story__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 700ms var(--easeOutQuint);
  pointer-events: none;

  .p-story__text:hover ~ .p-story__media & {
    @include hoverable {
      transform: scale(1.05);
    }
  }
}
