@use "../../foundation/libs/index" as *;

.c-semi-modal {
  top: auto;
  width: 100%;
  max-width: none;
  padding: 0;
  border: 0;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  text-align: center;
  animation-name: slideUp;
  animation-duration: 400ms;

  &::backdrop {
    background-color: #1c5a19b2;
    backdrop-filter: blur(4px);
  }
}

.c-semi-modal__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding-inline-start: 20px;
  border-bottom: 1px solid var(--color-background-brownLight);
  color: var(--color-primary-green);
  @include button-label-small;
}

.c-semi-modal__close {
  height: 100%;
  padding-inline: 20px;
}

.c-semi-modal__body {
  padding: 40px 15px;
}

@keyframes slideUp {
  0% {
    transform: translateY(100%);
  }

  100% {
    transform: translateY(0);
  }
}
