@use "../../foundation/libs/index" as *;

.p-related-post {
  display: grid;
  row-gap: 80px;
  border-top: 1px solid var(--color-primary-green-50);
  padding-block-start: 28px;

  @include mq(md, min) {
    margin-block-end: -10px;
  }

  @include mq(md) {
    row-gap: 60px;
    padding-block-start: 36px;
  }
}

.p-related-post__heading {
  @include heading-2;
}

.p-related-post__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  column-gap: 50px;
  row-gap: 40px;

  @include mq(md) {
    row-gap: 30px;
  }
}
