@use "../../foundation/libs/index" as *;

.p-post-header {
  display: grid;
  row-gap: 24px;
}

.p-post-header__heading {
  display: grid;
  row-gap: 20px;
}

.p-post-header__directory {
  font-family: var(--font-serif);
  font-size: 1.2rem;
  font-weight: normal;
  line-height: 1;
  letter-spacing: 0.1em;
}

.p-post-header__main-text {
  @include story-pc-heading-1;

  @include mq(md) {
    @include story-sp-heading-1;
  }
}

.p-post-header__tag-list {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  line-height: 1;
}
