@use "../../foundation/libs/index" as *;

.p-service-detail-story {
  width: 100%;
}

.p-service-detail-story__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  column-gap: 50px;
  row-gap: 40px;

  @include mq(md) {
    row-gap: 30px;
  }
}

.p-service-detail-story__item {
  display: grid;
  position: relative;
}

.p-service-detail-story__text {
  margin-block-start: 12px;
  @include card-title-small;
  transition: opacity 0.7s var(--easeOutQuint);
  text-overflow: ellipsis;
  overflow: hidden;

  &:hover {
    @include hoverable {
      opacity: 0.5;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 33px;
    left: 0;
  }
}

.p-service-detail-story__tag-list {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  line-height: 1;
  margin-block-start: 8px;
}

.p-service-detail-story__media {
  position: relative;
  aspect-ratio: 278/170;
  order: -1;
  overflow: hidden;
  pointer-events: none;

  @include mq(md) {
    aspect-ratio: 335/205;
  }
}
.p-service-detail-story__media.-podcast {
  &::after {
    content: url(../assets/static/images/common/icon_podcast.svg);
    position: absolute;
    right: 8px;
    bottom: 8px;
    width: 26px;
    height: 26px;
  }
}

.p-service-detail-story__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 700ms var(--easeOutQuint);
  pointer-events: none;

  .p-service-detail-story__text:hover ~ .p-service-detail-story__media & {
    @include hoverable {
      transform: scale(1.05);
    }
  }
}

.p-service-detail-story__footer {
  display: flex;
  justify-content: center;
  margin-block-start: 70px;

  @include mq(md) {
    margin-block-start: 40px;
  }
}
