@use "../../foundation/libs/index" as *;

.c-button-with-icon {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  color: var(--color-primary-green);
  transition: opacity 0.7s var(--easeOutQuint);
  @include button-label-small;

  &.-self-design {
    color: var(--color-concept-selfDesign);
  }

  &.-work-design {
    color: var(--color-concept-biz-blue);
  }

  &.-town-design {
    color: var(--color-concept-townDesign);
  }

  &:hover {
    @include hoverable {
      opacity: 0.5;
    }
  }
}

.c-button-with-icon__circle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.4rem;
  height: 2.4rem;
  border-radius: 1.2rem;
  background-color: var(--color-background-greige);

  .c-button-with-icon.-self-design & {
    background-color: var(--color-concept-selfDesign-20);
  }

  .c-button-with-icon.-work-design & {
    background-color: var(--color-concept-biz-blue-20);
  }

  .c-button-with-icon.-town-design & {
    background-color: var(--color-concept-townDesign-20);
  }
}
