@use "../../foundation/libs/index" as *;

.c-paging {
  position: relative;
  padding-block: 36px 80px;
  border-top: 1px solid var(--color-primary-green-20);

  @include mq(md) {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-block-start: 24px;
  }

  :where(& .prev-text, & .next-text) {
    @include mq(md, min) {
      @include visually-hidden;
    }
  }

  .nav-links {
    display: flex;
    justify-content: space-between;
    color: var(--color-primary-black);
    font-size: 1.4rem;
    line-height: 1.5;
    letter-spacing: 0.1em;
  }

  .nav-previous,
  .nav-next {
    max-width: 360px + 24px + 8px;
    background-image: url(../assets/static/images/common/icon_paging.svg);
    background-repeat: no-repeat;
    background-position: left center;
    transition: opacity 0.7s var(--easeOutQuint);

    @include mq(md) {
      background-position: left top;
      max-width: calc(50% - 10px);
    }

    &:hover {
      @include hoverable {
        opacity: 0.5;
      }
    }

    > a {
      display: flex;
      padding-inline-start: 24px + 8px;

      @include mq(md, min) {
        align-items: center;
        min-height: 24px;
      }

      @include mq(md) {
        flex-direction: column;
        gap: 8px;
      }
    }
  }

  .nav-next {
    text-align: right;
    transform: scale(-1, 1);
  }

  .next-text {
    text-align: right;
    transform: scale(-1, 1);
  }

  .prev-title,
  .next-title {
    overflow: hidden;

    @include mq(md, min) {
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    @include mq(md) {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 3;
      margin-left: -32px;
    }
  }

  .next-title {
    transform: scale(-1, 1);
  }
}

.c-paging__back {
  @include mq(md, min) {
    position: absolute;
    top: 36px;
    left: 50%;
    transform: translateX(-50%);
    line-height: 1;
    transition: opacity 0.7s var(--easeOutQuint);
  }

  @include mq(md) {
    display: inline-flex;
    align-items: center;
    gap: 4px;
    margin-block-start: 24px;
  }

  &:hover {
    @include hoverable {
      opacity: 0.5;
    }
  }
}

.c-paging__back-text {
  @include mq(md, min) {
    @include visually-hidden;
  }
}
