@use "../../foundation/libs/index" as *;

.p-service-detail-schedule {
  display: grid;
  row-gap: 17px;
  width: 100%;
  padding: 30px 40px 40px;
  border-radius: 4px;
  background-color: var(--color-background-greige);
  box-shadow: 0 -1px 0 0 var(--color-primary-green-20);

  @include mq(md) {
    padding: 16px 20px 20px;
  }
}

.p-service-detail-schedule__highlight {
  @include heading-3;
}

.p-service-detail-schedule__list {
  display: grid;
  grid-template-columns: 194px 1fr;
  border-top: 1px solid var(--color-primary-green-20);

  @include mq(md) {
    grid-template-columns: 100%;
  }
}

.p-service-detail-schedule__key {
  padding-block-start: 12px;
  padding-inline-end: 24px;
  @include body-bold;

  @include mq(md, min) {
    padding-block-end: 12px;
    border-bottom: 1px solid var(--color-primary-green-20);
  }
}

.p-service-detail-schedule__value {
  padding-block-end: 12px;
  border-bottom: 1px solid var(--color-primary-green-20);

  @include mq(md) {
    margin-block-start: 4px;
  }

  @include mq(md, min) {
    padding-block-start: 12px;
  }

  a:not([class]) {
    color: var(--color-other-textLink);
    transition: opacity 0.7s var(--easeOutQuint);

    &[target="_blank"] {
      padding-right: 24px;
      background-image: url(../assets/static/images/common/icon_external_blue.svg);
      background-position: right center;
      background-repeat: no-repeat;
    }

    &:hover {
      @include hoverable {
        opacity: 0.5;
      }
    }
  }
}
