@use "sass:math";
@use "../../foundation/libs/index" as *;

/* ------------------------------------------------- *
/*        p-about-kv1
/* ------------------------------------------------- */

.p-about-kv1__image1 {
  img {
    width: 100%;
  }
}

.p-about-kv1__image1__sp1 {
  @include mq(md, min) {
    display: none;
  }
}

.p-about-kv1__image1__pc1 {
  display: none;
  @include mq(md, min) {
    display: block;
  }
}

/* ------------------------------------------------- *
/*        p-about-kv2
/* ------------------------------------------------- */

.p-about-kv2__unit1 {
  height: 550px;
  background: var(--color-background-greige);
  display: grid;
  align-items: center;

  @include mq(md, min) {
    height: 500px;
  }
}

.p-about-kv2__unit1__inner1 {
  @include mq(md) {
    padding-inline: 35px;
  }
}

.p-about-kv2__unit1__title1 {
  font-family: var(--font-serif-jp);
  font-weight: 600;
  font-size: 3.8rem;
  line-height: 1.5;
  text-align: center;
  font-feature-settings: "palt" on;
  color: var(--color-other-concept-text);
  @include mq(md, min) {
    font-size: 4rem;
  }
  br {
    @include mq(md, min) {
      display: none;
    }
  }
}

.p-about-kv2__unit1__text1 {
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 1;
  text-align: center;
  letter-spacing: 0.1em;
  font-family: var(--font-serif);
  margin: 34px 0 0;
  @include mq(md, min) {
    margin: 48px 0 0;
  }
}

/* ------------------------------------------------- *
/*        p-about-block1
/* ------------------------------------------------- */

.p-about-block1 {
  margin: 100px 0 0;
  text-align: center;
  @include mq(md, min) {
    margin: 170px 0 0;
  }
  @at-root {
    .p-about-kv1 + & {
      margin: 130px 0 0;
    }
  }
}

.p-about-block1__inner1 {
  padding: 0 20px;
}

.p-about-block1__title1 {
  font-size: 4.4rem;
  font-family: var(--font-serif);
  font-weight: 400;
  letter-spacing: 0.1em;
  line-height: math.div(55, 44);
  @include mq(md, min) {
    font-weight: 400;
    font-size: 4.5rem;
    line-height: math.div(62.5, 50);
  }
  br {
    @include mq(md, min) {
      display: none;
    }
  }
  &.-type1 {
    letter-spacing: 0;
  }
}

.p-about-block1__text1 {
  margin: 32px 0 0;
  @include mq(md, min) {
    margin: 24px 0 0;
  }
}

.p-about-block1__text1_sp {
  @include mq(md, min) {
    display: none;
  }
}

.p-about-block1__text1_pc {
  display: none;
  @include mq(md, min) {
    display: block;
  }
}

.p-about-block1__text2 {
  margin: 32px 0 0;
  text-align: justify;
  font-size: 1.4rem;
  @at-root {
    .p-about-block1__title1 + & {
      margin-top: 42px;
    }
  }
  @include mq(md, min) {
    text-align: center;
    max-width: 744px;
    margin-left: auto;
    margin-right: auto;
  }
  br {
    display: none;
    @include mq(md, min) {
      display: block;
    }
  }
}

.p-about-block1__text3 {
  margin: 48px 0;
}

.p-about-block1__text4 {
  margin: 32px 0 0;
  text-align: justify;
  font-size: 1.4rem;
  @at-root {
    .p-about-block1__title1 + & {
      margin-top: 40px;
    }
  }
  @include mq(md, min) {
    text-align: left;
    max-width: 744px;
    margin-left: auto;
    margin-right: auto;
  }
  br {
    display: none;
    @include mq(md, min) {
      display: block;
    }
  }
}

/* ------------------------------------------------- *
/*        p-about-block2
/* ------------------------------------------------- */

.p-about-block2 {
  margin: 100px 0 0;
  padding: 36px 0 0;
  border-top: 1px solid var(--color-primary-green-50);
  scroll-margin: 60px;

  @include mq(md, min) {
    max-width: 934px;
    margin: 170px auto 0;
    scroll-margin: 320px;
  }
}

//Edge
@supports (-ms-ime-align: auto) {
  .p-about-block2 {
    scroll-margin-top: 0;

    @include mq(md) {
      scroll-margin-top: 0;
    }
  }
}

_:lang(x)::-ms-,
.p-about-block2 {
  scroll-margin-top: 0;

  @include mq(md) {
    scroll-margin-top: 0;
  }
}

//Safari
_::-webkit-full-page-media,
_:future,
:root .p-about-block2 {
  scroll-margin-top: 0;

  @include mq(md) {
    scroll-margin-top: 40px;
  }
}

//Firefox
@-moz-document url-prefix() {
  .p-about-block2 {
    scroll-margin-top: 0;

    @include mq(md) {
      scroll-margin-top: 40px;
    }
  }
}

.p-about-block2__inner1 {
  padding: 0 20px;
  @include mq(md, min) {
    padding: 0;
  }
}

.p-about-block2__title1 {
  font-weight: 500;
  font-size: 2.6rem;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.05em;
}

.p-about-block2__unit1__image1 {
  img {
    width: 100%;
  }
}

.p-about-block2__text1 {
  @include sp-sub-title-bold;
  margin: 28px 0 0;
  @include mq(md, min) {
    text-align: center;
    @include pc-sub-title-bold;
  }
  br {
    display: none;
    @include mq(md, min) {
      display: block;
    }
  }
}

.p-about-block2__list1 {
  display: grid;
  gap: 40px;
  padding: 0 15px;
  margin: 40px 0 0;
  @include mq(md, min) {
    margin: 48px 0 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;
    padding: 0;
  }
}

.p-about-block2__unit1 {
  &.-type1 {
    color: var(--color-concept-selfDesign);
  }
  &.-type2 {
    color: var(--color-concept-biz-blue);
  }
  &.-type3 {
    color: var(--color-concept-townDesign);
  }
}

.p-about-block2__unit1__inner1 {
  @include mq(md, min) {
    padding: 0 15px;
  }
}

.p-about-block2__unit1__title1 {
  font-weight: 500;
  font-size: 1.8rem;
  line-height: 1.25;
  letter-spacing: 0.1em;
  text-align: center;
  margin: 28px 0 0;
  @include mq(md, min) {
    margin: 32px 0 0;
  }
}

.p-about-block2__unit1__text1 {
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.8;
  text-align: justify;
  letter-spacing: 0.05em;
  margin: 28px 0 0;
  @include mq(md, min) {
    margin: 20px 0 0;
  }
}

.p-about-block2__unit1__link1 {
  margin: 28px 0 0;
  @include mq(md, min) {
    margin: 20px 0 0;
  }
}

/* ------------------------------------------------- *
/*        p-about-block3
/* ------------------------------------------------- */

.p-about-block3 {
  margin: 100px 0 0;
  padding: 36px 0 0;
  border-top: 1px solid var(--color-primary-green-50);
  @include mq(md, min) {
    max-width: 934px;
    margin: 170px auto 0;
