}

.p-about-block3__title1 {
  font-weight: 500;
  font-size: 2.6rem;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.1em;
}

.p-about-block3__unit1__image1 {
  & > img {
    width: 100%;
  }
}

.p-about-block3__text1 {
  @include sp-sub-title-bold;
  margin: 28px 0 0;
  @include mq(md, min) {
    text-align: center;
    @include pc-sub-title-bold;
  }
  br {
    display: none;
    @include mq(md, min) {
      display: block;
    }
  }
}

.p-about-block3__list1 {
  display: grid;
  gap: 24px;
  margin: 40px 0 0;
  @include mq(md, min) {
    margin: 80px 0 0;
    column-gap: 50px;
    row-gap: 40px;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.p-about-block3__unit1__title1-wrapper {
  @include mq(md, min) {
    overflow: hidden;
    width: 278px;
    height: 210px;
  }
}

.p-about-block3__unit1__title1 {
  font-family: var(--font-serif-jp);
  font-weight: 600;
  font-size: 2.4rem;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.05em;
  font-feature-settings: "palt" on;
  background: var(--color-background-greige);
  color: var(--color-other-concept-text);
  height: 180px;
  display: grid;
  align-items: center;
  transition: all 0.7s var(--easeOutQuint);
  will-change: transform;
  -webkit-font-smoothing: antialiased;
  transform: translate3d(0, 0, 0);
  &:hover {
    transform: scale(1.05);
    will-change: transform;
  }
  @include mq(md, min) {
    height: 210px;
  }
}

.p-about-block3__unit1__text1 {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5;
  text-align: right;
  letter-spacing: 0.05em;
  margin: 8px 0 0;
  font-family: var(--font-serif-jp);
}

