/* ------------------------------------------------- *
/*        p-about-block5
/* ------------------------------------------------- */

.p-about-block5 {
  margin: 100px 0 0;
  padding: 36px 0 0;
  border-top: 1px solid var(--color-primary-green-50);
  @include mq(md, min) {
    max-width: 934px;
    margin: 170px auto 0;
  }
}

.p-about-block5__inner1 {
  padding: 0 20px;
  @include mq(md, min) {
    padding: 0;
  }
}

.p-about-block5__title1 {
  font-weight: 500;
  font-size: 2.6rem;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.1em;
}

.p-about-block5__list1 {
  margin: 40px 0 0;
  display: grid;
  gap: 50px;
  @include mq(md, min) {
    margin: 80px 0 0;
    grid-template-columns: 1fr 1fr;
    row-gap: 60px;
    column-gap: 50px;
  }
}

.p-about-block5__unit1__text1 {
  @include heading-3;
  margin: 12px 0 0;
  display: flex;
  flex-direction: column;
  @include mq(md, min) {
    margin: 17px 0 0;
  }
  & > span {
    display: block;
    font-size: 1.4rem;
    line-height: 1.5;
    font-family: var(--font-serif);
    letter-spacing: 0.05em;
    margin: 6px 0 0;

    @include mq(md, min) {
      margin: 3px 0 0;
    }
  }
}

.p-about-block5__unit1__image1 {
  aspect-ratio: 442 / 300;
  & > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.p-about-block5__unit1__text2 {
  @include caption;
  margin: 12px 0 0;
  @include mq(md, min) {
    margin: 8px 0 0;
  }
}

.p-about-block5__unit1__title1 {
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 1.5;
  letter-spacing: 0.05em;
  font-family: var(--font-serif);
  margin: 12px 0 0;
}

.p-about-block5__unit1__list1 {
  margin: 8px 0 0;
  border-bottom: 1px solid var(--color-primary-green-20);
  & > li {
    border-top: 1px solid var(--color-primary-green-20);
    & > a {
      display: block;
      padding: 12px 20px 12px 0;
      @include card-title-xs;
      line-height: 1.5;
      position: relative;
      transition: all 0.7s var(--easeOutQuint);
      & > svg {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        margin: auto;
        transition: all 0.7s var(--easeOutQuint);
      }
      &:hover {
        opacity: 0.5;
        & > svg {
          right: -5px;
        }
      }
    }
  }
}

