/* ------------------------------------------------- *
/*        p-about-block7
/* ------------------------------------------------- */

.p-about-block7 {
  margin: 100px 0 0;
  @include mq(md, min) {
    margin: 130px 0 0;
  }
}

.p-about-block7__inner1 {
  padding: 0 35px;
  @include mq(md, min) {
    padding: 0;
  }
}
.p-about-block7 {
  .p-about-block7__item {
    @include mq(md, min) {
      display: flex;
      padding-top: 36px;
    }

    h2,
    p {
      @include mq(md, min) {
        margin-top: 0;
      }
    }
    h2 {
      @include mq(md, min) {
        width: 104px;
        padding-right: 28px;
        flex-shrink: 0;
      }
    }
  }

  .p-about-block7__speaker {
    padding: 0 0 35px 0;
    border-bottom: 1px solid rgba(28, 90, 25, 0.2);

    @include mq(md) {
      padding: 0 0 38px 0;
    }
  }
}

.p-about-block7__editor1 {
  @include mq(md, min) {
    width: 614px;
    margin: 0 auto;
  }
  h2 {
    font-weight: 500;
    font-size: 1.6rem;
    margin: 40px 0 0;
  }
  h2 + * {
    margin: 16px 0 0;
  }
  p {
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 2.2;
    letter-spacing: 0.1em;
    font-feature-settings: "palt" on;
    margin: 30px 0 0;

    @include mq(md) {
      margin: 18px 0 0;
    }
  }
  hr {
    width: 32px;
    height: 1px;
    border: 0;
  }

  .p-about-block7__speaker-text {
    @include mq(md) {
      margin-top: 3px;
    }
  }
}

.p-about-block7__text1 {
  margin: 100px -35px 0;
  background: var(--color-background-greige);
  padding: 30px;
  @include lead-copy-medium;
  @include mq(md, min) {
    width: 934px;
    margin: 100px auto 0;
    padding: 60px;
    @include lead-copy-large;
  }
}
