@use "sass:math";
@use "libs/index" as *;

*,
::before,
::after {
  box-sizing: border-box;
}

html {
  height: -webkit-fill-available;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 62.5%;
  text-size-adjust: 100%;
  line-height: 1.5;
}

body {
  min-height: 100vh;
  min-height: -webkit-fill-available;
  background-color: var(--color-background-beige);
  color: var(--color-primary-green);
  @include body;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
hr,
blockquote,
ol,
ul,
dl,
dd,
figure,
pre {
  margin: 0;
}

a {
  color: currentColor;
  text-decoration: none;
  text-underline-position: under;
}

img,
svg,
video,
canvas,
audio,
iframe,
embed,
object {
  max-width: 100%;
  vertical-align: bottom;
}

button {
  border: 0;
  background-color: transparent;
  color: inherit;
  font-family: inherit;
}

button:not(:disabled):not([aria-disabled="true"]) {
  cursor: pointer;
}

ol,
ul {
  padding: 0;
}

li {
  list-style-type: none;
}

textarea {
  border-radius: 0;
  resize: vertical;
}

fieldset {
  border: 0;
}

small {
  font-size: inherit;
}

em,
i,
address,
cite {
  font-style: normal;
}

sub {
  bottom: 0;
}

svg {
  overflow: visible;
  fill: currentColor;
}

table {
  border-collapse: collapse;
}

[hidden][hidden] {
  display: none;
}
