@use "sass:math";
@use "../../foundation/libs/index" as *;

.c-page-header {
  padding-block: calc(115px + var(--height-header)) 80px;
  background-color: var(--color-background-greige);

  @include mq(md) {
    padding-block-start: calc(184px + var(--height-header));
  }
}

.c-page-header__wrapper {
  max-width: 924px;
  margin-inline: auto;

  @include mq(md) {
    padding-inline: 24px;
  }

  &.-narrow {
    max-width: 772px;
  }
}

.c-page-header__heading {
  display: grid;
  row-gap: 20px;

  @include mq(md) {
    row-gap: 12px;
  }
}

.c-page-header__directory {
  color: var(--color-primary-black);
  font-family: var(--font-serif);
  font-weight: normal;
  font-size: 2rem;
  line-height: math.div(25, 20);
  letter-spacing: 0.1em;
}

.c-page-header__text-group {
  display: grid;
  @include mq(md, min) {
    white-space: nowrap;
  }
}

.c-page-header__sub-text {
  @include pc-sub-title-bold;

  @include mq(md) {
    @include sp-sub-title-bold;
  }
}

.c-page-header__main-text {
  display: block;
  font-size: 4rem;
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: 0.1em;
  font-feature-settings: "palt" on;

  @include mq(md) {
    @include sp-heading-1;
  }
}
