@use "../../foundation/libs/index" as *;

.p-service-body {
  @include mq(md) {
    margin-block-end: -30px;
  }
}

.p-service-body__contents {
  display: grid;
  row-gap: 18px;

  @include mq(md, min) {
    padding-block-end: 80px;
  }
}

.p-service-body__footer {
  display: none;

  @include mq(md) {
    display: flex;
    justify-content: center;
    padding-block-start: 60px;
  }
}

.p-service-body-button {
  @include mq(md) {
    &.is-hidden {
      display: none;
    }
  }
}
