@use "../../foundation/libs/index" as *;

.p-service-detail-description {
  max-width: 744px;
  margin-inline: auto;
}

.p-service-detail-description__text-group {
  p:not([class]):not(:first-child) {
    margin-block-start: 32px;
  }

  h3:not([class]):not(:first-child) {
    margin-block-start: 32px;
    font-weight: bold;
  }

  h4:not([class]) {
    &::before {
      content: "■";
    }

    &:not(:first-child) {
      margin-block-start: 48px;
    }
  }

  h4:not([class]) + p:not([class]),
  h4:not([class]) + ul:not([class]),
  h4:not([class]) + ol:not([class]) {
    margin-block-start: 0;
  }

  ul:not([class]):not(:first-child),
  ol:not([class]):not(:first-child) {
    margin-block-start: 32px;
    padding-inline-start: 24px;
  }

  li:not([class]) {
    list-style: inherit;
  }

  a:not([class]) {
    color: var(--color-other-textLink);
    text-decoration: underline;
    transition: opacity 0.7s var(--easeOutQuint);

    &[target="_blank"] {
      padding-right: 24px;
      background-image: url(../assets/static/images/common/icon_external_blue.svg);
      background-position: right center;
      background-repeat: no-repeat;
    }

    &:hover {
      @include hoverable {
        opacity: 0.5;
      }
    }
  }
}

.p-service-detail-description__link {
  margin-block-start: 28px;
  @include text-link;

  @include mq(md) {
    margin-block-start: 30px;
  }

  & + & {
    margin-block-start: 12px;
  }
}

.p-service-detail-section__tag-list {
  display: flex;
  flex-wrap: wrap;
  grid-area: tag;
  margin-block-start: 32px;
  line-height: 1;

  @include mq(md) {
    row-gap: 8px;
  }
}

.p-service-detail-section__tag-item {
  color: var(--color-other-darkBrown);
  font-size: 1.1rem;
  font-weight: 500;
  letter-spacing: 0.1em;

  &:nth-of-type(n + 2) {
    &::before {
      content: "/";
    }
  }
}
