@use "sass:math";
@use "../../foundation/libs/index" as *;

.p-top-hero {
  height: calc(100vh - 38px);
  background-size: 570px 570px;
  background-repeat: no-repeat;
  background-position: top 116px left calc(50% - 2px + 272px);

  @include mq(md, min) {
    min-height: 650px;
    max-height: 1200px;
  }

  @include mq(md) {
    @supports (height: 100svh) {
      height: calc(100svh - 38px);
    }
  }
}

.p-top-hero__slide-area {
  position: relative;
  width: 100%;
  height: 100%;
  background-image: url(../assets/static/images/top/hero.png);
  background-size: cover;
  background-position: center;
}

.p-top-hero__slide {
  width: 100%;
  height: 100%;
  position: relative;
}

.p-top-hero__image {
  position: absolute;
  right: 0;

  @include mq(md, min) {
    top: 80px;
    bottom: 0;
    width: 87.53vh;
    min-width: 570px;
  }

  @include mq(md) {
    right: 40px;
    left: 40px;
    bottom: 0;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.p-top-hero__text-group {
  display: grid;
  grid-template-areas: ". . ." ". message ." ". source .";
  height: 100%;
  position: relative;
  z-index: 1;

  @include mq(md, min) {
    grid-template-columns: 1fr 934px 1fr;
    grid-template-rows: 72px 1fr 72px;
  }

  @include mq(md) {
    grid-template-columns: 35px 1fr 35px;
    grid-template-rows: 83px 1fr 83px;
  }
}

.p-top-hero__message {
  grid-area: message;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-weight: 500;
  line-height: 2.3;
  letter-spacing: 0.02em;
  font-feature-settings: "palt" on, "pcap" on;
  font-family: var(--font-serif-accent);

  @include mq(md, min) {
    gap: 24px;
    font-size: 2.4rem;
  }

  @include mq(md) {
    gap: 12px;
    font-size: math.div(16, 375) * 100vw;
  }

  .js-comment {
    &:nth-child(even) {
      color: var(--color-concept-biz-blue);

      @include mq(md, min) {
        margin-inline-start: 214px;
      }
    }
  }
}

.p-top-hero__source {
  grid-area: source;
  font-family: var(--font-serif-accent);
  color: var(--color-primary-black);
  font-size: 1.2rem;
  line-height: 2.5;
  font-feature-settings: normal;

  @include mq(md) {
    font-size: math.div(10, 375) * 100vw;
  }

  p {
    opacity: 0;
    transition: opacity 0.5s linear;

    &.-show {
      opacity: 1;
    }
  }
}

.p-top-hero__link {
  &::after {
    content: "";
    display: block;
    position: absolute;
    inset: 0;
  }
}

.p-top-hero__pager {
  display: flex;
  justify-content: center;
  gap: 6px;
  margin-block-start: 18px;
}

.p-top-hero__bar {
  width: 50px;
  height: 2px;
  background-color: var(--color-other-grayBrown);

  @include mq(md) {
    width: 30px;
  }

  &[aria-current="true"] {
    background-color: var(--color-primary-green);
  }
}
