@use "../../foundation/libs/index" as *;

.p-not-found {
  display: grid;
  row-gap: 40px;
  padding-block-start: 100px;

  @include mq(md, min) {
    margin-block-end: -53px;
  }

  @include mq(md) {
    padding-block-start: 80px;
  }
}

.p-not-found__text {
  @include pc-sub-title;

  @include mq(md) {
    @include sp-sub-title;
  }
}
