@use "libs/media-query" as *;

:root {
  // == Colors
  --color-primary-green: #1c5a19;
  --color-primary-green-50: #1c5a1980;
  --color-primary-green-13: #1c5a1921;
  --color-primary-green-20: #1c5a1933;
  --color-primary-green-70: #1c5a19b2;
  --color-primary-white: #fff;
  --color-primary-black: #000;
  --color-concept-selfDesign: #2d914b;
  --color-concept-selfDesign-20: #2d914b33;
  --color-concept-biz-blue: #007995;
  --color-concept-biz-blue-20: #00799533;
  --color-concept-townDesign: #194b4b;
  --color-concept-townDesign-20: #194b4b33;
  --color-background-beige: #fbfaf5;
  --color-background-greige: #f2f1ed;
  --color-background-brownLight: #e0dcd4;
  --color-other-greige2: #edeae5;
  --color-other-grayBrown: #dbd6cc;
  --color-other-darkBrown: #8b867c;
  --color-other-yellow: #ffe150;
  --color-other-textLink: #2f96c2;
  --color-other-concept-text: #323232;

  // == Font Family
  --font-serif: "Marcellus", serif;
  --font-serif-jp: "Noto Serif JP", serif;
  --font-serif-accent: "Shippori Mincho B1", serif;

  // == Easing
  --easeInExpo: cubic-bezier(0.95, 0.05, 0.795, 0.035);
  --easeOutExpo: cubic-bezier(0.19, 1, 0.22, 1);
  --easeInOutExpo: cubic-bezier(1, 0, 0, 1);
  --easeInQuint: cubic-bezier(0.755, 0.05, 0.855, 0.06);
  --easeOutQuint: cubic-bezier(0.23, 1, 0.32, 1);
  --easeInOutQuint: cubic-bezier(0.86, 0, 0.07, 1);

  // == Height
  --height-header: 80px;

  @include mq(md) {
    --height-header: 60px;
  }
}
