@use "../../foundation/libs/index" as *;

.c-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 16px 35px;
  @include button-label;
  transition-property: color, background-color, opacity;
  transition-duration: 400ms;
  transition-timing-function: var(--easeOutExpo);

  &:disabled {
    opacity: 0.4;
  }

  &.-primary {
    border: 1px solid var(--color-primary-green);
    border-radius: 60px;
    color: var(--color-primary-green);
    opacity: 0.7;

    @include hoverable {
      &:hover {
        background-color: var(--color-primary-green);
        color: var(--color-primary-white);
        opacity: 1;
      }
    }
  }

  &.-for-biz {
    border: 1px solid var(--color-concept-biz-blue);
    border-radius: 60px;
    color: var(--color-concept-biz-blue);

    @include hoverable {
      &:hover {
        background-color: var(--color-concept-biz-blue);
        color: var(--color-primary-white);
      }
    }
  }

  &.-primary.-contained {
    background-color: var(--color-primary-green);
    color: var(--color-primary-white);
    opacity: 1;

    @include hoverable {
      &:hover {
        opacity: 0.5;
      }
    }
  }

  &.-fix {
    min-width: 245px;
  }

  &.-icon {
    position: relative;

    :where(&) svg {
      position: absolute;
      top: 50%;
      right: 16px;
      transform: translateY(-50%);
    }
  }
}
