@use "../../foundation/libs/index" as *;

.c-text-link-with-icon {
  color: var(--color-primary-green);
  text-decoration: underline;
  display: inline-grid;
  grid-template-columns: 20px 1fr;
  align-items: center;
  font-size: 1.4rem;
  transition: all 0.7s var(--easeOutQuint);

  svg {
    margin: 3px 0 0;
  }

  &:hover {
    @include hoverable {
      opacity: 0.5;
    }
  }
}
