@use "sass:math";
@use "../../foundation/libs/index" as *;

/* ------------------------------------------------- *
/*        p-top-block1
/* ------------------------------------------------- */

.p-top-block1 {
  margin: 100px 0 0;
  @include mq(md, min) {
    margin: 170px auto 0;
    max-width: 744px;
  }
}

.p-top-block1__inner1 {
  padding: 0 20px;
  @include mq(md, min) {
    padding: 0;
  }
}

.p-top-block1__title1 {
  font-family: var(--font-serif);
  font-weight: 400;
  font-size: 4.4rem;
  line-height: 1.25;
  text-align: center;
  letter-spacing: -0.01em;
  @include mq(md, min) {
    font-size: 5.3rem;
  }
  br {
    @include mq(md, min) {
      display: none;
    }
  }
}

.p-top-block1__text1 {
  margin: 40px 0 0;
}

.p-top-block1__button1 {
  margin: 40px 0 0;
  text-align: center;
}

/* ------------------------------------------------- *
/*        p-top-block2
/* ------------------------------------------------- */

.p-top-block2 {
  margin: 100px 0 0;
  padding: 36px 0 0;
  border-top: 1px solid var(--color-primary-green-50);
  @include mq(md, min) {
    max-width: 934px;
    margin: 170px auto 0;
  }
}

.p-top-block2__inner1 {
  padding: 0 20px;
  @include mq(md, min) {
    padding: 0;
  }
}

.p-top-block2__title1 {
  font-weight: 500;
  font-size: 2.6rem;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.1em;
  br {
    @include mq(md, min) {
      display: none;
    }
  }
}

.p-top-block2__text1 {
  @include sp-sub-title-bold;
  margin: 28px 0 0;
  @include mq(md, min) {
    text-align: center;
    @include pc-sub-title-bold;
  }
  br {
    display: none;
    @include mq(md, min) {
      display: block;
    }
  }
}

.p-top-block2__unit1 {
  margin: 60px 0 0;
  background-color: var(--color-background-greige);
  padding: 64px 24px;
  @include mq(md, min) {
    margin: 80px 0 0;
    padding: 60px 0;
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 384px;
  }
}

.p-top-block2__unit1__main1__image1 {
  @include mq(md, min) {
    max-width: 352px;
    margin: 0 auto;
  }
}

.p-top-block2__unit1__main1__link1 {
  text-align: center;
  margin: 24px 0 0;
  @include mq(md, min) {
    margin: 35px 0 0;
  }
}

.p-top-block2__unit1__nav1 {
  margin: 48px 0 0;
  @include mq(md, min) {
    margin: 0;
    padding: 0 54px 0 0;
  }
}

.p-top-block2__unit1__nav1__title1 {
  @include heading-4;
  border-top: 1px solid var(--color-other-grayBrown);
  padding: 24px 0 0;
  margin: 24px 0 0;
  &:first-child {
    @include mq(md, min) {
      margin: 0;
    }
  }
  &.-type1 {
    color: var(--color-concept-selfDesign);
    @include mq(md, min) {
      padding: 0;
      border: 0;
    }
  }
  &.-type2 {
    color: var(--color-concept-biz-blue);
  }
  &.-type3 {
    color: var(--color-concept-townDesign);
  }
}

.p-top-block2__unit1__nav1__list1 {
  margin: 12px 0 0;
}

/* ------------------------------------------------- *
/*        p-top-block3
/* ------------------------------------------------- */

.p-top-block3 {
  margin: 60px 0 0;
  @include mq(md, min) {
    max-width: 930px;
    margin: 80px auto 0;
  }
}

.p-top-block3__inner1 {
  padding: 0 20px;
  @include mq(md, min) {
    padding: 0;
  }
}

.p-top-block3__title1 {
  @include heading-3;
  text-align: center;
}

.p-top-block3__text1 {
  margin: 16px 0 0;
  @include mq(md, min) {
    text-align: center;
  }
}

.p-top-block3__list1 {
  margin: 28px 0 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 12px;
  @include mq(md, min) {
    margin: 20px 0 0;
    display: block;
    text-align: center;
  }

  & > div {
    @include mq(md, min) {
      display: inline-block;
      margin: 20px 8px 0;
    }
  }

  br {
    display: none;
    @include mq(md, min) {
      display: block;
    }
  }
}

.p-top-block3__label1 {
  display: inline-block;
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 1.25;
  text-align: justify;
  background-color: var(--color-background-greige);
  padding: 16px 24px;
  border-radius: 2px;
  transition: all 0.7s var(--easeOutQuint);

  &:hover {
    @include hoverable {
      background-color: var(--color-primary-green-13);
    }
  }
}

.p-top-block3__button1 {
  text-align: center;
  margin: 40px 0 0;
}

/* ------------------------------------------------- *
/*        p-top-block4
/* ------------------------------------------------- */

.p-top-block4 {
  margin: 100px 0 0;
  padding: 36px 0 0;
  border-top: 1px solid var(--color-primary-green-50);
  @include mq(md, min) {
    max-width: 934px;
    margin: 170px auto 0;
  }
}

.p-top-block4__inner1 {
  padding: 0 20px;
  @include mq(md, min) {
    padding: 0;
  }
}

.p-top-block4__text1 {
  @include sp-sub-title-bold;
  margin: 28px 0 0;

  @include mq(md, min) {
    text-align: center;
    @include pc-sub-title-bold;
  }

  br {
    display: none;
    @include mq(md, min) {
      display: block;
    }
  }
}

.p-top-block4__title1 {
  font-weight: 500;
  font-size: 2.6rem;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.1em;

  br {
    @include mq(md, min) {
      display: none;
    }
  }
}

.p-top-block4__list1-wrapper {
  margin: 60px 0 0;

  @include mq(md, min) {
    margin: 80px 0 0;
  }
}

.p-top-block4__list1 {
  margin: 60px 0 0;
  display: grid;
  grid-template-columns: 100%;
  gap: 40px;

  @include mq(md, min) {
    margin: 80px 0 0;
    grid-template-columns: 278px 278px 278px;
    column-gap: 50px;
    row-gap: 40px;
  }
}

.p-top-block4__unit1__main1 {
  display: block;
}

.p-top-block4__unit1__main1__image1 {
  position: relative;
  aspect-ratio: 278 / 170;
  display: block;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.7s var(--easeOutQuint);
  }

  svg {
    position: absolute;
    bottom: 8px;
    right: 8px;
  }

  @at-root {
    .p-top-block4__unit1__main1:hover & img {
      transform: scale(1.1);
    }
  }
}

.p-top-block4__unit1__main1__text1 {
  text-overflow: ellipsis;
  overflow: hidden;
  @include card-title-small;
  display: block;
  margin: 12px 0 0;
  transition: all 0.7s var(--easeOutQuint);

  @at-root {
    .p-top-block4__unit1__main1:hover {
      .p-top-block4__unit1__main1__text1 {
        opacity: 0.5;
      }
    }
  }
}

.p-top-block4__tag-list {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  line-height: 1;
  margin-block-start: auto;
  padding-block-start: 8px;
}

.p-top-block4__unit1__label1 {
  margin: 8px 0 0;
}

.p-top-block4__button1 {
  margin: 60px 0 0;
  text-align: center;

  @include mq(md, min) {
    margin: 80px 0 0;
  }
}

/* ------------------------------------------------- *
/*        p-top-block5
/* ------------------------------------------------- */

.p-top-block5 {
  margin: 100px 0 0;
  padding: 36px 0 0;
  border-top: 1px solid var(--color-primary-green-50);

  @include mq(md, min) {
    max-width: 934px;
    margin: 170px auto 0;
  }
}

.p-top-block5__inner1 {
  padding: 0 20px;

  @include mq(md, min) {
    padding: 0;
  }
}

.p-top-block5__title1 {
  font-weight: 500;
  font-size: 2.6rem;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.1em;

  br {
    @include mq(md, min) {
      display: none;
    }
  }
}

.p-top-block5__wrap1 {
  margin: 60px 0 0;
  @include mq(md, min) {
    margin: 80px 0 0;
    display: grid;
    grid-template-columns: 294px 1fr;
    gap: 26px;
  }
}

.p-top-block5__main1__text1 {
  @include sp-sub-title-bold;

  @include mq(md, min) {
    @include pc-sub-title-bold;
  }
  margin: 24px 0 0;
}

.p-top-block5__main1__text2 {
  font-size: 1.4rem;
  margin: 12px 0 0;
}

.p-top-block5__main1__link1 {
  margin: 12px 0 0;
}

.p-top-block5__map1 {
  margin: 24px 0 0;
  height: 340px;
  overflow: hidden;

  & > iframe {
    width: 100%;
    height: 700px;
    margin: -170px 0 0;
    filter: sepia(10%);
  }
}
