@use "sass:math";
@use "../../foundation/libs/index" as *;

.c-radio-tag {
  display: inline-flex;
  padding: 0;

  :where(&) svg {
    width: 16px;
    height: 16px;
    margin-bottom: -1px;
  }
}

.c-radio-tag__input {
  @include visually-hidden;
}

.c-radio-tag__label {
  display: inline-flex;
  gap: 4px;
  padding: 7px 16px 8px;
  font-size: 1.2rem;
  line-height: math.div(15, 12);
  letter-spacing: 0.05em;
  font-feature-settings: normal;
  border-radius: 15px;
  background-color: var(--color-other-greige2);
  color: var(--color-other-darkBrown);
  cursor: pointer;

  .c-radio-tag__input:checked + &,
  .c-radio-tag.-active & {
    background-color: var(--color-other-darkBrown);
    color: var(--color-primary-white);
  }

  .c-radio-tag__input + & > svg {
    display: none;
  }

  .c-radio-tag__input:checked + & > svg {
    display: block;
  }
}

.c-radio-tag__remove {
  .c-radio-tag__input ~ .c-radio-tag__label &,
  .c-radio-tag & {
    display: none;
  }

  .c-radio-tag__input:checked ~ .c-radio-tag__label &,
  .c-radio-tag.-active & {
    @include visually-hidden;
  }
}
