@use "../foundation/libs/index" as *;

.l-contents {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 80px 1fr 254px;

  @include mq(md, min) {
    min-width: 1024px;
    overflow: auto;
  }

  @include mq(md) {
    grid-template-rows: 60px 1fr 577px;
  }

  main > * {
    &:last-child {
      margin-bottom: 130px;

      @include mq(md, min) {
        margin-bottom: 240px;
      }
    }
  }
}

.l-contents__wrapper {
  width: 100%;
  max-width: 934px;
  margin-inline: auto;

  @include mq(md) {
    padding-inline: 20px;
  }

  &.-narrow {
    max-width: 772px;
  }
}
