@use "../../foundation/libs/index" as *;

.p-service-hagukumu {
  display: flex;
  align-items: center;
  gap: 92px;
  height: 555px;
  margin-block-end: -70px;
  padding-inline: 102px 54px;
  border: 1px solid var(--color-other-greige2);
  background-color: var(--color-background-greige);
}

.p-service-hagukumu__figure-group {
  align-self: flex-start;
  padding-block: 90px 73px;
}

.p-service-hagukumu__forest {
  margin-block-start: 35px;
  text-align: center;
  line-height: 1;
}

.p-service-hagukumu__text-group {
  flex-grow: 1;
}

.p-service-hagukumu__design-group {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding-block: 24px;

  & + & {
    border-top: 1px solid var(--color-other-grayBrown);
  }
}

.p-service-hagukumu__heading {
  @include heading-4;

  &.-self-design {
    color: var(--color-concept-selfDesign);
  }

  &.-work-design {
    color: var(--color-concept-biz-blue);
  }

  &.-town-design {
    color: var(--color-concept-townDesign);
  }
}

.p-service-hagukumu__list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  line-height: 1;
}
