@use "../../foundation/libs/index" as *;

.p-service-header {
  display: grid;
  padding-block: 80px 40px;

  @include mq(md) {
    padding-block: 60px 24px;
  }
}

.p-service-header__tab-list {
  display: flex;
  flex-wrap: wrap;

  @include mq(md, min) {
    gap: 8px;
    border-bottom: 1px solid var(--color-primary-green-20);
  }

  @include mq(md) {
    gap: 12px;
    padding-block-end: 24px;
  }
}

.p-service-header__tag-list {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;

  @include mq(md) {
    padding-top: 24px;
    border-top: 1px solid var(--color-background-brownLight);
  }
}

.p-service-header__filter-area {
  display: grid;
  gap: 24px;

  @include mq(md) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

.p-service-header__filter-button {
  display: inline-flex;
  gap: 8px;
  padding: 11px 19px;
  border-radius: 21px;
  border: 1px solid var(--color-background-brownLight);
  @include button-label-small;
}
