@use "sass:math";
@use "../../foundation/libs/index" as *;

.p-header {
  position: relative;
  background-color: transparent;
}

.p-header__home-link {
  padding: 20px;
  line-height: 1;

  :where(&) > svg {
    @include mq(md) {
      width: 138px;
      height: 20px;
    }
  }
}

.p-header__menu-button {
  position: absolute;
  top: 0;
  right: 0;
  padding: 21px 20px;
  z-index: z(menu-button);

  &[aria-expanded="true"] {
    position: fixed;
  }

  :where(&) path {
    transition-property: transform, opacity;
    transition-duration: 400ms;
    transition-timing-function: var(--easeOutExpo);
  }

  :where(&[aria-expanded="true"]) path:nth-child(1) {
    transform: rotate(45deg) translate(1px, -1px) scaleX(1.3);
  }

  :where(&[aria-expanded="true"]) path:nth-child(2) {
    opacity: 0;
  }

  :where(&[aria-expanded="true"]) path:nth-child(3) {
    transform: rotate(-45deg) translate(-12px, -4px) scaleX(1.3);
  }
}

.p-header__menu {
  @include mq(md) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 50px;
    position: fixed;
    inset: 0;
    padding-block-start: 60px;
    background-color: var(--color-background-brownLight);
  }
}

.p-header__main-list {
  display: inline-flex;
  gap: 48px;
  color: var(--color-primary-green);
  font-family: var(--font-serif);
  font-size: 2rem;
  line-height: math.div(25, 20);

  @include mq(md) {
    display: block;
    width: 300px;
    margin-inline: auto;
    border-top: 1px solid var(--color-primary-green-20);
    text-align: center;
  }
}

.p-header__main-item {
  @include mq(md) {
    border-bottom: 1px solid var(--color-primary-green-20);
  }

  &.-for-biz {
    @include mq(md, min) {
      color: var(--color-concept-biz-blue);
    }
  }
}

.p-header__main-link {
  display: block;
  padding-block: 8px;
  position: relative;
  letter-spacing: 0;

  @include mq(md) {
    padding: 22px;
  }

  &::before {
    @include mq(md, min) {
      content: "";
      display: block;
      position: absolute;
      bottom: 0;
      right: 0;
      width: 0;
      height: 1px;
      background-color: currentColor;
      transition: all 0.7s var(--easeOutQuint);
    }
  }

  &:hover::before {
    @include mq(md, min) {
      width: 100%;
      left: 0;
    }
  }
}

.p-header__sns-list {
  display: flex;
  justify-content: center;
  gap: 18px;
}

.p-header__sns-link {
  display: block;
  border-radius: 9px;
  background-color: var(--color-primary-white);
  color: var(--color-primary-green);
}
